@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&family=Playfair+Display&display=swap);
*{margin:0;padding:0;box-sizing:border-box}h1,h2,h3{font-family:'Playfair Display', serif;color:#363636}p{font-family:'Montserrat', sans-serif}.App{transition:all 0.5s ease}.library-active{margin-left:30%}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:20%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}@media screen and (max-width: 767px){.song-container img{width:60%}}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.player input[type=range]{height:34px;-webkit-appearance:none;margin:10px 0;width:100%}.player input[type=range]:focus{outline:none}.player input[type=range]::-webkit-slider-runnable-track{width:100%;height:11px;cursor:pointer;animate:0.2s;box-shadow:1px 1px 1px #000000;background:#74a9d8;border-radius:1px;border:0px solid #010101}.player input[type=range]::-webkit-slider-thumb{box-shadow:1px 1px 1px #000031;border:1px solid #00001e;height:26px;width:26px;border-radius:15px;background:#ffffff;cursor:pointer;-webkit-appearance:none;margin-top:-8px}.player input[type=range]:focus::-webkit-slider-runnable-track{background:#74a9d8}.player input[type=range]::-moz-range-track{width:100%;height:11px;cursor:pointer;animate:0.2s;box-shadow:1px 1px 1px #000000;background:#74a9d8;border-radius:1px;border:0px solid #010101}.player input[type=range]::-moz-range-thumb{box-shadow:1px 1px 1px #000031;border:1px solid #00001e;height:26px;width:26px;border-radius:15px;background:#ffffff;cursor:pointer}.player input[type=range]::-ms-track{width:100%;height:11px;cursor:pointer;animate:0.2s;background:transparent;border-color:transparent;color:transparent}.player input[type=range]::-ms-fill-lower{background:#74a9d8;border:0px solid #010101;border-radius:2px;box-shadow:1px 1px 1px #000000}.player input[type=range]::-ms-fill-upper{background:#74a9d8;border:0px solid #010101;border-radius:2px;box-shadow:1px 1px 1px #000000}.player input[type=range]::-ms-thumb{margin-top:1px;box-shadow:1px 1px 1px #000031;border:1px solid #00001e;height:26px;width:26px;border-radius:15px;background:#ffffff;cursor:pointer}.player input[type=range]:focus::-ms-fill-lower{background:#74a9d8}.player input[type=range]:focus::-ms-fill-upper{background:#74a9d8}.time-control{width:50%;display:flex}.time-control input{width:100%;padding:1rem 0rem}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:30rem}.play-control svg{cursor:pointer}@media screen and (max-width: 767px){.time-control{width:90%}.play-control{width:60%}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background-color:white;box-shadow:none;overflow:visible;-webkit-transform:translateX(-100%);transform:translateX(-100%);transition:all 0.5s ease;opacity:0}.library h2{padding:2rem}.library-open{box-shadow:2px 2px 50px gray;-webkit-transform:translateX(0);transform:translateX(0);opacity:1}.song{display:flex;padding:1rem 2rem 1rem 2rem;cursor:pointer}.song img{width:30%}.song:hover{background-color:lightblue}.song-description{padding-left:1rem;font-family:'Montserrat', sans-serif}.song-description h3{font-size:1rem;font-weight:bold}.song-description h4{font-size:0.7rem}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{scrollbar-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.selected{background-color:violet;border-top-right-radius:12px;border-bottom-right-radius:12px;left:10px;width:105%}@media screen and (max-width: 767px){.library{width:100%}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;border:none;cursor:pointer;border:2px solid #414141;padding:0.5rem;transition:all 0.3s ease}nav button:hover{background:#414141;color:white}@media screen and (max-width: 767px){nav button{z-index:10}}

