.player {
	min-height: 20vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;

	input[type=range] {
		height: 34px;
		-webkit-appearance: none;
		margin: 10px 0;
		width: 100%;
	}
	input[type=range]:focus {
		outline: none;
	}
	input[type=range]::-webkit-slider-runnable-track {
		width: 100%;
		height: 11px;
		cursor: pointer;
		animate: 0.2s;
		box-shadow: 1px 1px 1px #000000;
		background: #74a9d8;
		border-radius: 1px;
		border: 0px solid #010101;
	}
	input[type=range]::-webkit-slider-thumb {
		box-shadow: 1px 1px 1px #000031;
		border: 1px solid #00001e;
		height: 26px;
		width: 26px;
		border-radius: 15px;
		background: #ffffff;
		cursor: pointer;
		-webkit-appearance: none;
		margin-top: -8px;
	}
	input[type=range]:focus::-webkit-slider-runnable-track {
		background: #74a9d8;
	}
	input[type=range]::-moz-range-track {
		width: 100%;
		height: 11px;
		cursor: pointer;
		animate: 0.2s;
		box-shadow: 1px 1px 1px #000000;
		background: #74a9d8;
		border-radius: 1px;
		border: 0px solid #010101;
	}
	input[type=range]::-moz-range-thumb {
		box-shadow: 1px 1px 1px #000031;
		border: 1px solid #00001e;
		height: 26px;
		width: 26px;
		border-radius: 15px;
		background: #ffffff;
		cursor: pointer;
	}
	input[type=range]::-ms-track {
		width: 100%;
		height: 11px;
		cursor: pointer;
		animate: 0.2s;
		background: transparent;
		border-color: transparent;
		color: transparent;
	}
	input[type=range]::-ms-fill-lower {
		background: #74a9d8;
		border: 0px solid #010101;
		border-radius: 2px;
		box-shadow: 1px 1px 1px #000000;
	}
	input[type=range]::-ms-fill-upper {
		background: #74a9d8;
		border: 0px solid #010101;
		border-radius: 2px;
		box-shadow: 1px 1px 1px #000000;
	}
	input[type=range]::-ms-thumb {
		margin-top: 1px;
		box-shadow: 1px 1px 1px #000031;
		border: 1px solid #00001e;
		height: 26px;
		width: 26px;
		border-radius: 15px;
		background: #ffffff;
		cursor: pointer;
	}
	input[type=range]:focus::-ms-fill-lower {
		background: #74a9d8;
	}
	input[type=range]:focus::-ms-fill-upper {
		background: #74a9d8;
	}
}
.time-control {
	width: 50%;
	display: flex;

	input {
		width: 100%;
		padding: 1rem 0rem;
	}
	p {
		padding: 1rem;
	}
}

.play-control {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 1rem;
	width: 30rem;

	svg {
		cursor: pointer;
	}
}

@media screen and (max-width: 767px) {
	.time-control {
		width: 90%;
	}
	.play-control {
		width: 60%;
	}
}
